.footer{
   
    background-color: #181823;
}
.footer_box{
    width: 100%;
    height: 300px;
    padding: 72px;
    padding-top: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.footer_box-nav{
    width: 400px;
    height: 100px;
}
.footer_nav-list{
    height: 100%;
    width: 200px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 40px;
}
.footer_nav-items{
    height: 40px;
    ;
}
.footer_nav-link{
    height: 100%;
    width: 100%;
    fill: #C1A1AF;
}
.footer_icon{
    width: 100%;
    height: 100%;
}
.footer_img-wechat{
    border-radius: 10px;
    width: 25%;
    height: 25%;
}
